'use strict';

if (module.hot) {
  module.hot.accept();
}

import 'babel-polyfill';
import '../styles/index.scss';
import StandBuilder from './standBuilder.js';

const standBuilder = new StandBuilder();
let labs = {
	"Lab_1": {
		"elements": [
			{
				"type": "Input",
				"x": 100,
				"y": 100
			},
			{
				"type": "Input",
				"x": 100,
				"y": 200
			},
			{
				"type": "Resistor",
				"x": 250,
				"y": 300,
				"resistance": 100,
			}
		]
	},
	"Lab_2": {
		"elements": [
			{
				"type": "Input",
				"x": 300,
				"y": 100
			},
			{
				"type": "Input",
				"x": 100,
				"y": 200
			},
			{
				"type": "Resistor",
				"x": 250,
				"y": 300,
				"resistance": 100,
			}
		]
	}
};
let LabList = document.getElementById("selectLab");
let sendBtn = document.getElementById("sendBtn");
sendBtn.addEventListener('click', (e) => {
	console.log(standBuilder.toJSON());
	let data =	[
			{
				type: "Input",
				x: 300,
				y: 100
			},
			{
				type: "Input",
				x: 100,
				y: 200
			},
			{
				type: "Wire",
				input_1_id: '1',
				input_2_id: '2'
			}
		];
	$.ajax({
		type: "POST",
		url: "http://localhost:3000/check",
		xhrFields: {
        	withCredentials: false
	    },
	    crossDomain: true,
	    contentType: 'application/json',
		data: JSON.stringify(data)
	}).done((msg) => {
		alert("Answer = " + JSON.stringify(msg));
	});
});
LabList.addEventListener('change', (e) => {
	changeStand();
});
changeStand();

function changeStand() {
	if (standBuilder.isDirty === true) {
		standBuilder.clear();
	}
	let selectId = LabList.options.selectedIndex;
	let labId = LabList.options[selectId].value;
	let selectedLab = labs[labId];
	let data = selectedLab;
	standBuilder.build(data);
}

