import { Line, Circle } from 'konva';
import Screen from './Screen.js';

class Wire extends Line {
	constructor(points, ctx) {
		super({
			points: points,
			stroke: 'red',
			stokeWidth: 3
		});
		this.type = 'Wire';
		this.connected = false;
		this.x = (points[2] + points[0]) / 2;
		this.y = (points[3] + points[1]) / 2;
		this.id = this.type + ' #' + Math.floor(Math.random() * 1000);
		this.context = ctx;
		this.tooltip = null;
		this.on('mouseover', this.mouseOverHandler.bind(this));
		this.on('mouseout', this.mouseOutHandler.bind(this));
	}
	mouseOverHandler(e) {
		if (this.connected && !this.tooltip) {
			this.tooltip = new Screen(this.x, this.y, 'black', '#f2f2f2', '12 A');
			this.context.addElement(this.tooltip);
		}
	}
	mouseOutHandler(e) {
		if (this.tooltip) {
			this.tooltip.destroy();
			this.tooltip = null;
			this.context.draw();
		}
	}
	update(points) {
		this.x = (points[4] + points[0]) / 2;
		this.y = (points[5] + points[1]) / 2;
		return points;
	}
}

export default Wire;


