import { Rect } from 'konva';
import Screen from './Screen.js';
import Input from './Input.js';

class Resistor extends Rect {
	constructor(x, y, resistance, ctx) {
		super({
			x: x,
			y: y,
			width: 50,
			height: 25,
			strokeWidth: 1,
			fill: 'rgba(12,12,12,0.1)',
			stroke: 'black'
		});
		this.tooltip = null;
		this.context = ctx;
		this.resistance = resistance;
		this.x = x;
		this.y = y;

		this.inputs = [
			new Input(this.x - 20, this.y + 12.5, this.context),
			new Input(this.x + 70, this.y + 12.5, this.context)
		];
		this.context.addElements(this.inputs);
		this.on('mouseover', this.mouseOverHandler.bind(this));
		this.on('mouseout', this.mouseOutHandler.bind(this));
	}

	mouseOverHandler(e) {
		if (!this.tooltip) {
			this.tooltip = new Screen(this.x + 25, this.y + 12.5, 'black', '#f2f2f2', `${this.resistance} `, 0.8);
			this.context.addElement(this.tooltip);
		}
	}
	mouseOutHandler(e) {
		if (this.tooltip) {
			this.tooltip.destroy();
			this.tooltip = null;
			this.context.draw();
		}
	}

}

export default Resistor;


