import { Circle, Line } from 'konva';
import Wire from './Wire.js';

class Input extends Circle {
	constructor(x, y, ctx) {
		super({
			x: x,
			y: y,
			radius: 8,
			strokeWidth: 2,
			fill: '#f2f2f2',
			stroke: 'black'
		});
		/* attributes */
		this.x = this.attrs.x;
		this.y = this.attrs.y;
		this.radius = 10;
		this.context = ctx;

		/* info */
		this.type = 'Input';
		this.id = this.type + ' #' + Math.floor(Math.random() * 1000);
		this.used = false;
		this.connectInput = null;
		this.connecting = false;

		/* handlers */
		this.on('mouseover', this.mouseOverHandler);
		this.on('mouseout', this.mouseOutHandler);
		this.on('dblclick', this.dblClickHandler);
	}
	connect(input) {
		this.used = true;
		this.connectInput = input;
		this.wire = input.wire;
		if(this.pointer) this.pointer.destroy();
	}
	disconnect() {
		this.used = false;
		this.pointer = null;
		this.connectInput = null;
	}
	mouseOverHandler(e) {
		//console.log(this.id + ' mouse over');
		if (!this.pointer && !this.used) {
			this.fill('#f4d742');
			this.context.draw();
			this.pointer = new Circle({
				x: this.x,
				y: this.y,
				radius: 8,
				fill: 'red',
				draggable: true
			});
			this.context.addElement(this.pointer);
			this.pointer.on('dragstart', this.startPointer.bind(this));
			this.pointer.on('dragmove', this.updatePointer.bind(this));
			this.pointer.on('dragend', this.connectPointer.bind(this));
		}
	}
	mouseOutHandler(e) {
		this.fill('#f2f2f2');
		this.context.draw();
	}
	dblClickHandler(e) {
		console.log('Double clicked...' + this.used);
		if (this.used) {
			this.wire.destroy();
			this.connectInput.disconnect();
			this.disconnect();
			this.context.draw();
		}
	}
	startPointer(e) {
		let x = e.evt.offsetX;
		let y = e.evt.offsetY;
		console.log(`Start => x: ${x}, y: ${y}`);
		this.wire = new Wire([	
			this.x,
			this.y,
			(this.x + x) / 2,
			this.y,
			(this.x + x) / 2,
			y,		
			x,
			y
			],
			this.context
		);
		this.context.addElement(this.wire);
		this.context.draw();
	}
	updatePointer(e) {
		let x = e.evt.offsetX;
		let y = e.evt.offsetY;
		this.wire.points(
			this.wire.update([	
				this.x,
				this.y,
				(this.x + x) / 2,
				this.y,
				(this.x + x) / 2,
				y,		
				x,
				y
				]
			)
		);
		this.context.draw();
	}

	connectPointer(e) {
		let x = e.evt.offsetX;
		let y = e.evt.offsetY;
		let connected = false;
		console.log(`Connect => x: ${x}, y: ${y}`);
		this.wire.points([	
			this.x,
			this.y,
			(this.x + x) / 2,
			this.y,
			(this.x + x) / 2,
			y,		
			x,
			y
			]);
		console.log('Checking elements...');
		for (let el of this.context.getElements()) {
			if (el.type === 'Input') {
				console.log(`${el.id} x:${el.x} 
					y:${el.y} with r:${el.radius}`);
				if (el.x + el.radius*2 >= x && el.x - el.radius*2 <= x 
					&& el.y - el.radius*2 <= y && el.y + el.radius*2 >= y)
						if (el.used === false) {
							el.connect(this);
							this.connect(el);
							connected = true;
							this.wire.connected = true;
							this.wire.points([	
								this.x,
								this.y,
								(this.x + x) / 2,
								this.y,
								(this.x + x) / 2,
								y,		
								el.x,
								el.y
							]);
							this.pointer.destroy();
							this.context.draw();
							console.log(`Element - ${el.id}`);
							console.log('Connected... Success');
							return true;
					}				
			}
		}
		console.log('Connection failure...');
		if (connected === false) {
			this.wire.destroy();
			this.pointer.destroy();
			this.connecting = false;
			this.used = false;
			this.pointer = null;
			this.context.draw();
			return false;
		}
	}
}

export default Input;


