import { Line, Circle, Label } from 'konva';
import Screen from './Screen.js';

class Regulator extends Circle {
	constructor(x, y, data, ctx) {
		super({
			x: x,
			y: y,
			//width: 120,
			//height: 120,
			radius: 20,
			strokeWidth: 1,
			fill: 'rgba(12,12,12,0.1)',
			stroke: 'black'
		});
		this.x = x;
		this.y = y;
		this.context = ctx;
		this.pointer = new Line({
			points: [this.x, this.y, this.x, this.y - 20],
			stroke: 'black',
			strokeWidth: 1
		});
		this.data = data;
		this.states = this.data.map((el, index) => {
			let label = new Screen(this.x + Math.cos(index * 180 / this.data.length) * 20, this.y + Math.sin(index * 180 / this.data.length) * 20, 'yellow', 'black', el, 0.8);
			label.rotate(index * 180 / this.data.length);
			this.context.addElement(label);
			
			return {
				'rotation': index * 180 / this.data.length,
				'data': el,
				'label': label
			};
		});

		this.stateId = 2;
		this.curState = this.states[this.stateId];
		console.log(`Curstate ${JSON.stringify(this.curState)}`);
		this.context.addElement(this.pointer);
		this.spining = false;
		console.log(`states - ${JSON.stringify(this.states)}`);
		

	 	this.on('mousedown', (e) => {
	 		this.spining = true;
        });

	 	this.on('mousemove', (e) => {
	 		if (this.spining === true) {
	 			if (e.evt.offsetX >= this.x) {
	 				this.spinRight();
	 			} else {
	 				this.spinLeft();
	 			}
	 		}
	 	});

        this.on('mouseup', (e) => {
        	this.spining = false;
        });
    }

    spinRight() {
    	console.log('Spining right');
    	if (!this.stateId == this.data.length - 1) {
    		this.curState = this.states[++this.stateId];
    		/*this.pointer.points([this.x, this.y, Math.sin(this.curState.rotation) * 20, Math.cos(this.curState.rotation) * 20]);*/
    		this.rotate(this.curState.rotation);
    		this.context.draw();
    	} else {
    		return false;
    	}
    	
    }
    spinLeft() {
    	console.log('Spining left');
    	if (!this.stateId == 0) {
    		this.curState = this.states[--this.stateId];
    		this.rotate(this.curState.rotation);
    		    		this.context.draw();

    	} else {
    		return false;
    	}
    }
    check() {
    	console.log(`${this.spining}`);
    }
}

export default Regulator;


