import { Label, Text, Tag } from 'konva';

class Screen extends Label {
	constructor(x, y, textColor, backgroundColor, text, scale = 1) {
		super({
			x: x,
			y: y,
			opacity: 1
		});
		console.log(`Creating ${this.className}`);
		this.type = 'Screen';
		this.id = this.type + ' #' + Math.floor(Math.random() * 1000);
		this.add(new Tag({
			fill: backgroundColor,
			stroke: '#333',
			strokeWidth: 1,
			pointerDirection: 'down',
	        pointerWidth: 10 * scale,
	        pointerHeight: 10 * scale,
	        lineJoin: 'round'
		}));
		this.add(new Text({
			text: text,
			fontFamily: 'Calibri',
			fontSize: 18 * scale,
			padding: 5 * scale,
			fill: textColor
		}));
	}
}

export default Screen;


