import Stand from './StandComponents/Stand.js';
import Input from './StandComponents/Input.js';
import Screen from './StandComponents/Screen.js';
import Regulator from './StandComponents/Regulator.js';
import Resistor from './StandComponents/Resistor.js';
import Inductivity from './StandComponents/Inductivity.js';
import Capacity from './StandComponents/Capacity.js';

class StandBuilder {
	constructor() {
		this.stand = new Stand();
		this.isDirty = false;
	}

	build(data) {
		this.isDirty = true;
		for (let el of data.elements) {
			this.stand.addElement(this.createElement(el));
		}
	}
	clear() {
		this.isDirty = false;
		this.stand.elements.map((e) => {
			e.destroy();
		});
	}
	createElement(el) {
		let element;
		switch (el.type) {
			case 'Input':
				element = new Input(el.x, el.y, this.stand);
				break;
			case 'Screen':
				element = new Screen(el.x, el.y, el.textColor, el.backgroundColor, el.text);
				break;
			case 'Regulator':
				element = new Regulator(el.x, el.y, el.data, this.stand);
				break;
			case 'Resistor':
				element = new Resistor(el.x, el.y, el.resistance, this.stand);
				break;
			default:
				break;
		}
		return element;
	}
	toJSON() {
		let elements = this.stand.getElements();
		let standJSON = [];
		for (let element of elements) {
			standJSON.push({
				type: element.type ? element.type : 'null',
				id: element.id ? element.id : 'null',
			});
		}
		return (standJSON);
	}
}

export default StandBuilder;


