import { Layer, Stage } from 'konva';

class Stand extends Layer {
	constructor() {
		super();
		let container = document.querySelector('#stand');
		let width = container.offsetWidth;
	    let height = container.offsetHeight;
		this.stage = new Stage({
			container: 'stand',
	      	width: width,
	      	height: height
		});
		this.elements = [];
	}

	addElement(element) {
		this.add(element);
		this.stage.add(this);
		this.elements.push(element);
	}
	addElements(elements) {
		for (let element of elements) {
			this.add(element);
			this.elements.push(element);
		}
		this.stage.add(this);
	}
	getElements() {
		return this.elements;
	}
}

export default Stand;


